import axios from "axios";
import { Order } from "./types";

const api = axios.create({
  baseURL: "http://localhost:4000/orders", // your backend orders route
  headers: {
    "Content-Type": "application/json",
    // Add token if needed
    Authorization: `Bearer ${localStorage.getItem("token") || ""}`,
  },
});

// GET /orders
export const getOrders = () => api.get<Order[]>("");

// POST /orders
export const createOrder = (data: Partial<Order>) => api.post("", data);

// PUT /orders/:id
export const updateOrder = (id: string, data: Partial<Order>) => api.put(`/${id}`, data);

// DELETE /orders/:id
export const deleteOrder = (id: string) => api.delete(`/${id}`);
